import { DateRangeModel } from './../../model/shared/daterange.model';
import { Injectable } from "@angular/core";
import { HttpService } from "../shared/http.service";
import { environment } from 'src/environments/environment';
import { SERVICE_URL } from './../service.constant';

@Injectable({
  providedIn: 'root'
})
export class BookingAvailableService {

    constructor(private http: HttpService) {

    }
    getAllAvailabelSlots(dateRangeModel: DateRangeModel ) {
        return this.http.post(SERVICE_URL.BASE_APP_URL + 'assistant/available', 'post', dateRangeModel);
    }
}
