import { Injectable } from "@angular/core";
import { HttpService } from "../shared/http.service";
import { SERVICE_URL } from './../service.constant';

@Injectable()
export class ServiceBookingService {

    constructor(private http: HttpService) {

    }

    fetchLocations() {
        return this.http.fetch(SERVICE_URL.BASE_APP_URL + 'map/', 'get');
    }

    fetchSABasedOnLocation(locationId: string) {
        return this.http.fetch(SERVICE_URL.BASE_APP_URL + 'map/' + locationId, 'get');
    }
}
