import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {ServiceAvailibilityModel} from '../../../../../rina-lib/src/lib/model/service.availiblity.model';
import {WindowToogleService} from '../../../../../rina-lib/src/lib/services/alfahim/window.toggle.service';
import {PersonalDetailsService} from "../../../../../rina-lib/src/lib/services/alfahim/personal.details.service";

@Component({
  selector: 'service-list-component',
  templateUrl: 'service-list.component.html'
})

export class ServiceListComponent implements OnInit {
  serviceModel: ServiceAvailibilityModel;
  @Input('service-list') serviceList: any[] = [];
  @Output() search: EventEmitter<any> =  new EventEmitter<any>();
  @Output() errorFound: EventEmitter<any> =  new EventEmitter<any>();
  currentstep = 2;
  windowEnable = false;
  errorMsgData = [];
  constructor(private _windowTService: WindowToogleService,
              private _personalDetailsService: PersonalDetailsService) {
    this.serviceModel = new ServiceAvailibilityModel();
  }

  ngOnInit() {
  }

  onPrevious(event: any) {
   this.onCloseClick();
  }

  scheduleAppointment(serviceModel: ServiceAvailibilityModel) {
    this._personalDetailsService.setPeferredLocation(serviceModel);
    this.currentstep = this._windowTService.getNextWindow(2);
  }

  getSearchObject(event: any) {
    if (isNaN(event.searchId)) {
      this.serviceList[0].disabled = true;
      this.errorMsgData.push('Please enter number.');
      this.errorFound.emit(event);
    } else {
      this.search.emit(event);
    }
  }

  openServiceBookingWindow(service: any) {
    if (service.serviceName === 'SERVICE BOOKING') {
      this.windowEnable = true;
      this.currentstep = 2;
    }
  }

  onServiceConfirmPrevious(event: any) {
    this.currentstep = this._windowTService.getPreviousWindow(3);
  }
  onCloseClick() {
    this.currentstep = -1;
    this.windowEnable = false;
  }
  onAppointmentConfirm(event: any) {
    this.currentstep = this._windowTService.getNextWindow(3);
  }
}
